.i{
    display: flex;
    height: 100vh;
}

.i-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right{
    flex: 1;
    position: relative;
}

.i-left-wrapper{
    padding: 50px;
    height: 50%;
    /* display: flex; */
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-desc{
    font-size: large;
    padding: 20px 0;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100px;
    animation: move 6s ease-in-out infinite alternate;
}

@keyframes move {
    50%{
        transform: translateY(-50px);
    }
}

.i-title-item{
    height:50px;
    font-size: 30px;
    font-weight: bold;
    color: green;
    display: flex;
    align-items: center;
}

.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

.i-img{
    width: 50vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    bottom: 0;
    right: 15vh;
}

.i-bg{
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: #59b256;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

@media screen and (max-width: 480px){
    .i{
        flex-direction: column;
    }

    .i-left-wrapper{
        padding: 30px 10px 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }

    .i-desc{
        display: none;
    }

    .i-img{
        width: 50vh;
        height: 40vh;
        object-fit: contain;
        position: absolute;
        bottom: 0;
        right: 5vh;
    }
    
    .i-title-wrapper{
        padding: 0 5vh;
    }
}